
a {
  color: white;
}
.loginWrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgb(51, 51, 51);
  .goback {
    position: absolute;
    right: 5px;
    top: 8px;
    
  }
  .goback .icon {
    font-size: 1.5em;
    fill: aliceblue
  }
  header {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
    padding-bottom: 30px;
  }
  header .name>img {
    height: 45px;
  }
  header h2 {
    font-weight: normal;
    color: rgb(153, 153, 153)
  }
  .box {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid;
    border-radius: 4px;
    margin-left: 50%;
    transform: translateX(-50%);
    width: 80%;
    padding-top: 26px;
    padding-bottom: 26px;
    border-color: rgb(81, 81, 81);
  }
  .box .icon {
    margin-right: 5px;
  }
  .validation {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    color: rgba(255, 255, 255, 0.8);
    font-size: 14px;
    margin-top: 30px;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .validation>.username>.label {
    margin-left: -22%;
    margin-bottom: 2px;
  }
  .validation>.password>.label {
    margin-left: -31%;
    margin-bottom: 2px;
  }
  .validation div {
    text-align: center;
    width: 100%;
  }
  .validation .password {
    margin: 20px
  }
  .validation input,
  button {
    border: none;
    outline: none;
    width: 80%;
    border-radius: 4px;
    padding: 7px;
  }
  .validation button {
    color: rgba(255, 255, 255, 0.8);
    background: rgba(234, 76, 136, .9)
  }
  .validation button:active {
    background: rgba(234, 76, 136, 1)
  }
  .box .item {
    width: 80%;
    border-radius: 4px;
    text-align: center;
    padding: 7px;
    color: white;
  }
  .box .item:nth-child(2) {
    margin: 20px;
    background-color: rgb(59, 89, 152)
  }
  .box .item:nth-child(1) {
    background-color: rgb(29, 161, 242)
  }
  .box .item:nth-child(3) {
    background-color: rgb(66, 133, 244)
  }
}
