* {
  margin: 0;
  padding: 0;
  box-sizing: border-box
}
.app {
    position: relative;
    width: 100%;
}
.icon {
    width: 1em; height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}
a {
    text-decoration-line: none;
}
ul, ol {
    list-style: none;
}